  span {
    height: 75px;
    width: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: limegreen;
    color: white;
    font-size: 20px;
    border: 5px solid limegreen;
    border-radius: 50%;
    margin: auto 10px;
  }
  
  /* Another dose of dynamic className goodness is the winning ticket */
 span.selected {
    background-color: white;
    color: purple;
    border: 5px solid purple;
  }