
  
  button {
    height: 30px;
    width: 140px;
    background-color: purple;
    color: white;
    border-radius: 4px;
    text-transform: uppercase;
    cursor: pointer;
    outline: none;
  }
  
  button:hover {
    background-color: limegreen;
  }
  
  /* Adding a class of 'selected' to the selected button dynamically is secret sauce */
  button.active {
    background-color: white;
    color: limegreen;
    border: none;
    cursor: default;
  }